<template>
	<view id="register" class="bg-color">
		<view class="register-block">
			<block-bar-other title="修改个人信息" />
			<view class="action-block">
				<view class="action-item">
					<view class="title">学号/工号</view>
					<text>22920212204274</text>
				</view>
				<view class="action-item">
					<view class="title">姓名</view>
					<text>姚珅</text>
				</view>
				<view class="action-item">
					<view class="title">性别</view>
					<radio-group @change="selectGender" class="radio-box">
						<label class="radio-item">
							<radio value="男" :checked="gender === '男'" color="#39c5bb"
								style="transform:scale(0.7)" />
							男
						</label>
						<label class="radio-item">
							<radio value="女" :checked="gender === '女'" color="#39c5bb"
								style="transform:scale(0.7)" />
							女
						</label>
					</radio-group>
				</view>
				<view class="action-item">
					<view class="title">年级</view>
					<picker  @change="pickerChange" mode="date" fields="year" class="picker-box">
						<text v-if="grade">{{ grade }}级</text>
						<text v-else style="color: #666;">请选择入学年份</text>
					</picker>
				</view>
				<view class="action-item">
					<view class="title">学院</view>
					<input v-model="department" class="input-box" type="text" placeholder="输入所在学院" />
				</view>
				<view class="action-item">
					<view class="title">专业</view>
					<input v-model="major" class="input-box" type="text" placeholder="输入所在专业" />
				</view>
				<view class="action-item">
					<view class="title">电话</view>
					<input v-model="phone" class="input-box" type="text" placeholder="输入联系电话" />
				</view>
				<button @click="modify" class="button">提交</button>
			</view>
		</view>
	</view>
</template>

<script setup>
import { ref } from 'vue'
import { globalData } from '@/utils/config.js'

// 双向绑定的数据
const gender = ref('')
const grade = ref('')
const department = ref('')
const major = ref('')
const phone = ref('')
// 选择性别
const selectGender = (e) => {
	gender.value = e.detail.value
}

const pickerChange = (e) => {
	grade.value = parseInt(e.detail.value);
}

const userInfo = uni.getStorageSync('userInfo');
uni.request({
	url: globalData.baseUrl + '/user/getInfo/' + userInfo.token,
	header: {
		'Content-Type': 'application/json'
	},
	success: (res) => {
		const backData = res.data;
		if(backData.code === 0) {
			gender.value =  backData.data.gender;
			department.value =  backData.data.department;
			major.value =  backData.data.major;
			phone.value =  backData.data.phone;
			grade.value =  backData.data.grade;
		}
		else {
			uni.showToast({
				title: "认证错误，请重新登录",
				icon: "error"
			});
			uni.removeStorageSync("userInfo");
			uni.redirectTo({
			    url: "/pages/user/login" 
			});
		}
	},
	fail: (e)=> {
		uni.showToast({ title: "网络异常", icon: 'error' })
	}
})


const modify = () => {	
	
}
</script>

<style lang="less" scoped>
#register {
	.register-block {
		.action-block {
			margin: 20rpx 0;
			padding: 10rpx 30rpx;
			background-color: #fff;

			.action-item {
				display: flex;
				align-items: center;
				padding: 20rpx 0;
				border-bottom: 1rpx solid #ccc;

				text,.input-box {
					flex: 1;
					font-size: 32rpx;
					margin: 0 20rpx;
				}

				.radio-box {
					flex: 1;
					font-size: 32rpx;
					margin: 0 20rpx;

					.radio-item {
						margin-right: 20rpx;
					}
				}

				.picker-box {
					flex: 1;
					font-size: 32rpx;
					margin: 0 20rpx;
				}
			}

			.tip-block {
				border: 0;
				font-size: 24rpx;
				color: gray;

				.blue {
					margin-left: 5rpx;
					color: #39c5bb;
					font-weight: bold;
				}
			}

			.button {
				font-size: 32rpx;
				margin: 10rpx 0;
				border-radius: 50rpx;
				color: #fff;
				background-color: #39c5bb;
				border: none
			}
		}
	}
}
</style>